* {
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body,
#root,
.route,
.index {
  width: 100%;
  height: 100%;
}

.index .headerNav {
  position: relative;
  width: 1364px;
  height: 50px;
  line-height: 50px;
  background: #2b5295;
  color: #fff;
}

.index .headerNav img {
  position: absolute;
  left: 0;
  top: 0;
  width: 136px;
  height: 50px;
}

.index .headerNav ul {
  float: right;
  margin-right: 202px;
}

.index .headerNav ul li {
  float: left;
  width: 100px;
  text-align: center;
  height: 50px;
}

.index .headerNav .user {
  position: absolute;
  top: 0;
  right: 152px;
}

.index .headerNav .info {
  position: absolute;
  top: 0;
  right: 20px;
}

.index .userinfo {
  float: left;
  width: 140px;
  height: 250px;
}

.index .userinfo img {
  display: block;
  width: 120px;
  height: 120px;
}

.index .tipBlock {
  float: left;
  border: 1px solid #ccc;
  width: 300px;
  height: 200px;
  border-radius: 10px;
}
